
export interface Card {
    _id: string;
    name: string;
}

export interface Deck {
    created: string;
    author: string | {
        _id: string;
        username: string;
    };
    skill: {
        _id: string;
        name: string;
    };
    main: Array<{
        card: Card;
        amount: number;
    }>;
    extra: Array<{
        card: Card;
        amount: number;
    }>;
    url: string;
    deckType: {
        tier?: number | null;
        name: string;
    };
    rankedType?: {
        name: string;
        shortName: string;
        icon: string;
        statsWeight: number;
    };
    tournamentType?: {
        shortName: string;
        name: string;
        enumSuffix: string;
        icon: string;
        statsWeight: number;
    };
    tournamentPlacement?: string;
    tournamentNumber?: string;
    engines: string[];
    side: Array<{
        card: Card;
        amount: number;
    }>;
    gemsPrice: number;
    dollarsPrice: number;
}

export interface MonthlyDecks {
    decks: Deck[];
    deckTypeImage: Array<[string, string]>;
}

export const DeckTypeImageSize = 320;
